import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { map, tap, shareReplay, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { Observable, Subject } from 'rxjs';
import { User } from 'src/app/shared/models/User';
import { UserService } from 'src/app/shared/services/user/user.service';
import { Router } from '@angular/router';
import { ScriptsService } from 'src/app/shared/services/client/scripts.service';
import { DeviceService } from 'src/app/shared/services/client/device.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  @Input() filterByRole: string = '';
  @Input() showCreateFormTitle: boolean = false;
  @Output() totalUsers: EventEmitter<number> = new EventEmitter<number>();
  showCreateForm: boolean = false;
  url: string = 'admin/user-role/all-users';
  orderby: string = 'status';
  search: string = '';
  p: any = 0;
  showingcount: any = 10;
  searchTerm$: Subject<string> = new Subject<string>();
  code: any = `${this.ss.generateRandomAlphanumeric('10')}`
  users$: Observable<User[]> = this._us.getUsersApi$.pipe(
    map(
      (users: User[]) => {
        if (this.filterByRole.length > 0) {
          return users.filter((u: User) => u.role.map(r => r.toLowerCase()).includes(this.filterByRole.toLowerCase().trim()))
        }
        return users;
      }
    ),
    tap((res: any) => {
      this.totalUsers.emit(res.length);
    }),
    shareReplay(1)
  );

  searchIntervalTimer: number = 1000;
  loaderConfig: any = {
    color: 'accent',
    mode: 'query',
    value: 0,
    bufferValue: 75,
    inprogress: null,
  }
  _search:string = ``;
  constructor(private _us: UserService, private _ds: DeviceService, private ss: ScriptsService, private router: Router) {
    this.totalUsers.emit(0);
  }

  ngOnInit(): void {

    this._us.getUsersApi$
    this.users$.subscribe()
    this.searchTerm$.pipe(
      debounceTime(1000),
      distinctUntilChanged()
    ).subscribe(_terms => this.triggerSearch(_terms))
  }
  triggerSearch(terms:any){
    if(!this.loaderConfig.inprogress){
      this.loaderConfig.value = 1;
      this.loaderConfig.inprogress = setInterval(()=>{

        let nextcount = Math.floor(this.loaderConfig.value + (this.searchIntervalTimer/100));

        if(nextcount < 60){
          this.loaderConfig.value = nextcount;
        }else if(nextcount >99){
          this.loaderConfig.value = 0;
          clearInterval(this.loaderConfig.inprogress);
          this.loaderConfig.inprogress = null;
          this._search = this.search;
          this._ds.oInfoNotification('Result', `Search completed`)
        }else{
          this.loaderConfig.value = this.loaderConfig.value + Math.floor(nextcount/3)
        }

      }, this.searchIntervalTimer)
    }else{

    }
  }
  closeForm(e: any) {
    e.preventDefault();
    this.showCreateForm = !this.showCreateForm;
    if (!this.showCreateForm) {
      // this.router.navigate([this.url])
    }
  }

  saveCompleted(event: any) {
    this.showCreateForm = false;
  }
  viewUser(user: any) {

    this.router.navigate([`admin/user-role/user/view/${user.code}`]);
  }

}
