import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from 'src/app/shared/services/user/user.service';
import { switchMap, catchError, shareReplay, map } from 'rxjs/operators';
import { Observable, of, throwError } from 'rxjs';
import { User } from 'src/app/shared/models/User';
@Component({
  selector: 'app-user-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
  @Input('refreshOnRouteChange') refreshOnRouteChange: boolean = true;
  @Input('user') user: any = null;
  _default: any = {
    code: '',
    firstName: '',
    lastName: '',
    gender: '',
    marital_status: '',
    country: '',
    phone: '',
    address: '',
    state: '',
    city: '',
    date_of_employment: '',
    dob: '',
    email: '',
    password: '',
    passport: '',
    role: '',
    job_title: '',
    job_positions: '',
    // reports_to: '',
    department: '',
    type_of_contract: '',
    working_hours: '',
    cfale: '',
    ale: '',
    ala: '',
    sort_code: '',
    location: '',
    probation: '',
    confidance: '',
    result: '',
    comments: '',
    behavior: '',
    amount: '',
    currency: '',
    pay_type: '',
    payroll: '',
    pension: '',
    name_of_acc: '',
    name_of_bank: '',
    acc_num: '',
  }
  includeHeader: boolean = true;
  user$: Observable<any> = this.route.paramMap;
  constructor(private route: ActivatedRoute, private router: Router, private us: UserService) { }

  ngOnInit(): void {
    this.user$ = this.user$.pipe(
      map(paramMap => paramMap.get('code')),
      switchMap((code: any) => {
        if (this.refreshOnRouteChange) {
          return this.us.getSortedUsersApi({ code }).pipe(
            map((_users: any) => {
              let user = this._default;
              const found = _users.find((u: any) => u.code.toLowerCase().includes(code.toLowerCase()));
              if (found.code) {
                user = { ...user, ...found };
              }
              return Object.assign(user, user)
            }),
            catchError((err: any) => {
              return throwError(new Error('Issues with getting request from server'))
            }),
            shareReplay(1)
          )
        }
        return of({ ...this._default, code })
      }),
      catchError((err: any) => {
        return throwError(new Error('Issues with getting code from route'))
      }),
      shareReplay()
    )
    this.user$
      .subscribe(
        result => {

        },
        error => console.log(error)
      )
  }

}
